import React, { createContext, useContext, useState, useEffect } from "react";

const ThemeContext = createContext();

export const useTheme = () => {
  const context = useContext(ThemeContext);
  if (!context) {
    throw new Error("useTheme must be used within a ThemeProvider");
  }
  return context;
};

export const ThemeProvider = ({ children }) => {
  //    
  const getSystemTheme = () => {
    return window.matchMedia("(prefers-color-scheme: dark)").matches;
  };

  //    ( )
  const getInitialTheme = () => {
    const saved = localStorage.getItem("theme");
    if (saved === "dark") return true;
    if (saved === "light") return false;
    if (saved === "system") return getSystemTheme();
    return getSystemTheme(); // :   
  };

  const [isDarkMode, setIsDarkMode] = useState(getInitialTheme);
  const [themeMode, setThemeMode] = useState(() => {
    const saved = localStorage.getItem("theme");
    return saved || "system";
  });

  //    
  useEffect(() => {
    const mediaQuery = window.matchMedia("(prefers-color-scheme: dark)");

    const handleSystemThemeChange = (e) => {
      if (themeMode === "system") {
        setIsDarkMode(e.matches);
      }
    };

    mediaQuery.addEventListener("change", handleSystemThemeChange);
    return () =>
      mediaQuery.removeEventListener("change", handleSystemThemeChange);
  }, [themeMode]);

  // DOM  
  useEffect(() => {
    const root = document.documentElement;
    const themeColorMeta = document.getElementById("theme-color");

    //    transition 
    root.style.setProperty("--tw-transition-duration", "0ms");

    if (isDarkMode) {
      root.classList.add("dark");
      //     
      if (themeColorMeta) {
        themeColorMeta.setAttribute("content", "#111827");
      }
    } else {
      root.classList.remove("dark");
      //     
      if (themeColorMeta) {
        themeColorMeta.setAttribute("content", "#ffffff");
      }
    }

    // transition 
    setTimeout(() => {
      root.style.removeProperty("--tw-transition-duration");
    }, 0);

    //   
    localStorage.setItem("theme", themeMode);
    localStorage.setItem("isDarkMode", JSON.stringify(isDarkMode));
  }, [isDarkMode, themeMode]);

  //   
  const toggleDarkMode = () => {
    const newMode = isDarkMode ? "light" : "dark";
    setThemeMode(newMode);
    setIsDarkMode(!isDarkMode);
  };

  const setTheme = (mode) => {
    setThemeMode(mode);
    switch (mode) {
      case "dark":
        setIsDarkMode(true);
        break;
      case "light":
        setIsDarkMode(false);
        break;
      case "system":
        setIsDarkMode(getSystemTheme());
        break;
      default:
        setIsDarkMode(false);
    }
  };

  const value = {
    isDarkMode,
    isDark: isDarkMode, // DarkModeToggle   
    theme: themeMode,
    themeMode,
    toggleDarkMode,
    toggleTheme: toggleDarkMode, // DarkModeToggle   
    setTheme,
    systemTheme: getSystemTheme(),
  };

  return (
    <ThemeContext.Provider value={value}>{children}</ThemeContext.Provider>
  );
};
