import React, { createContext, useContext, useState, useEffect } from "react";
import { Amplify } from "aws-amplify";
import {
  signUp,
  signIn,
  signOut,
  getCurrentUser,
  fetchAuthSession,
  confirmSignUp,
  resendSignUpCode,
  resetPassword,
  confirmResetPassword,
} from "aws-amplify/auth";
import awsConfig from "../aws-config";

// Amplify 
Amplify.configure(awsConfig);

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error("useAuth must be used within an AuthProvider");
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState(false);
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    checkAuthStatus();
  }, []);

  const checkAuthStatus = async () => {
    try {
      setLoading(true);
      const currentUser = await getCurrentUser();
      const session = await fetchAuthSession();

      //    
      const groups = session?.tokens?.idToken?.payload["cognito:groups"] || [];
      const userRole = groups.includes("admin") ? "admin" : "user";

      // ID    
      const email = session?.tokens?.idToken?.payload?.email || currentUser.username;
      const userName = email.includes("@") ? email.split("@")[0] : email;

      console.log("  :", {
        userId: currentUser.userId,
        username: currentUser.username,
        email: email,
        groups: groups,
        userRole: userRole,
        fullPayload: session?.tokens?.idToken?.payload
      });

      setIsAuthenticated(true);
      setUser({
        id: currentUser.userId,
        email: email,
        name: userName,
        role: userRole,
        groups: groups,
      });
    } catch (error) {
      console.log("  :", error);
      setIsAuthenticated(false);
      setUser(null);
    } finally {
      setLoading(false);
    }
  };

  const login = async (credentials) => {
    try {
      const { email, password } = credentials;
      
      console.log(" :", { email });

      const user = await signIn({ username: email, password });
      
      console.log("signIn :", user);

      if (user.isSignedIn === false) {
        if (user.nextStep?.signInStep === "CONFIRM_SIGN_IN_WITH_NEW_PASSWORD_REQUIRED") {
          console.log("  ");
          throw new Error("  ");
        } else if (user.nextStep?.signInStep === "CONFIRM_SIGN_UP") {
          console.log("  ");
          throw new Error("  ");
        } else {
          console.log("  -    :", user.nextStep);
          throw new Error(" ");
        }
      }

      console.log("   ...");
      await checkAuthStatus();

      console.log(" ");
      return { success: true, user };
    } catch (error) {
      console.error(" :", error);

      let errorMessage = " .";

      if (error.name === "NotAuthorizedException") {
        errorMessage = "    .";
      } else if (error.name === "UserNotConfirmedException") {
        errorMessage = "  .";
      } else if (error.name === "UserNotFoundException") {
        errorMessage = "  .";
      } else if (error.message) {
        errorMessage = error.message;
      }

      throw new Error(errorMessage);
    }
  };

  const signup = async (userData) => {
    try {
      const { email, password, fullname } = userData;

      const result = await signUp({
        username: email,
        password: password,
        options: {
          userAttributes: {
            email: email,
            name: fullname || email.split("@")[0],
          },
        },
      });

      return {
        success: true,
        message:
          " .     .",
        userSub: result.userId,
      };
    } catch (error) {
      console.error(" :", error);

      let errorMessage = " .";

      if (error.name === "UsernameExistsException") {
        errorMessage = "  .";
      } else if (error.name === "InvalidPasswordException") {
        errorMessage =
          "   . ( 8,    )";
      } else if (error.message) {
        errorMessage = error.message;
      }

      throw new Error(errorMessage);
    }
  };

  const logout = async () => {
    try {
      await signOut();
      setIsAuthenticated(false);
      setUser(null);
    } catch (error) {
      console.error(" :", error);
      //    
      setIsAuthenticated(false);
      setUser(null);
    }
  };

  const verifyEmail = async (verificationData) => {
    try {
      const { email, code } = verificationData;

      await confirmSignUp({ username: email, confirmationCode: code });

      return {
        success: true,
        message: "  . .",
      };
    } catch (error) {
      console.error("  :", error);

      let errorMessage = "  .";

      if (error.name === "CodeMismatchException") {
        errorMessage = "   .";
      } else if (error.name === "ExpiredCodeException") {
        errorMessage = "  .";
      } else if (error.message) {
        errorMessage = error.message;
      }

      throw new Error(errorMessage);
    }
  };

  const resendVerificationCode = async (email) => {
    try {
      await resendSignUpCode({ username: email });
      return {
        success: true,
        message: "   .",
      };
    } catch (error) {
      console.error("   :", error);
      throw new Error("   .");
    }
  };

  const forgotPassword = async (email) => {
    try {
      await resetPassword({ username: email });
      return {
        success: true,
        message: "    .",
      };
    } catch (error) {
      console.error("  :", error);
      throw new Error("  .");
    }
  };

  const confirmPassword = async (resetData) => {
    try {
      const { email, code, newPassword } = resetData;

      await confirmResetPassword({ 
        username: email, 
        confirmationCode: code, 
        newPassword: newPassword 
      });

      return {
        success: true,
        message: "  .",
      };
    } catch (error) {
      console.error("  :", error);

      let errorMessage = "  .";

      if (error.name === "CodeMismatchException") {
        errorMessage = "   .";
      } else if (error.name === "ExpiredCodeException") {
        errorMessage = "  .";
      } else if (error.message) {
        errorMessage = error.message;
      }

      throw new Error(errorMessage);
    }
  };

  //  
  const refreshToken = async () => {
    try {
      const session = await fetchAuthSession();
      return session;
    } catch (error) {
      console.error("  :", error);
      setIsAuthenticated(false);
      setUser(null);
      throw error;
    }
  };

  // API    
  const getAuthToken = async () => {
    try {
      const session = await fetchAuthSession();
      return session?.tokens?.idToken?.toString();
    } catch (error) {
      console.error("  :", error);
      return null;
    }
  };

  const value = {
    isAuthenticated,
    user,
    loading,
    login,
    signup,
    logout,
    verifyEmail,
    resendVerificationCode,
    forgotPassword,
    confirmResetPassword: confirmPassword,
    checkAuthStatus,
    refreshToken,
    getAuthToken,
  };

  return <AuthContext.Provider value={value}>{children}</AuthContext.Provider>;
};
